@import url(https://fonts.googleapis.com/css?family=Raleway);
*{font-family:"Raleway", sans-serif}.container{width:1024px;border:1px solid rgba(0,0,0,0.2);box-shadow:0px 2px 10px rgba(0,0,0,0.1);text-align:center;border-radius:10px;padding:20px;box-sizing:border-box;top:0px;bottom:0px;left:0px;right:0px;margin:auto}.title{position:relative;background-color:#e7bbbb;color:#882323;margin:50px;padding:30px;font-size:1.8em;border-radius:5px}.questionBox{position:relative;border:1px solid rgba(0,0,0,0.2);box-sizing:border-box;background-color:#fafafa}.question{font-size:1.2em;font-weight:bold;margin-top:20px}.answerBtn{position:relative;margin:15px;padding:10px;border:1px solid #882323;opacity:0.4;background:none;display:inline-block;outline:none;font-size:1em;color:#882323;font-weight:bold;border-radius:4px;cursor:pointer}.answerBtn:hover{transition:0.25s;opacity:1;background:#882323;color:white}.answerBtn:target{transition:0.5s;background:#232346;border-color:#232346}.score-board{position:relative;height:700px}.score{position:absolute;width:1024px;font-size:3em;top:250px;text-align:center}.playBtn{position:absolute;font-size:1.4em;padding:10px;top:350px;background-color:#2eb62e;color:#fff;width:150px;left:0px;right:0px;margin:auto;outline:none;cursor:pointer}

